import MakananSource from '../../data/restaurantdb-source';
import { createRestaurantItemTemplate } from '../templates/template-creator';

const Home = {
  async render() {
    return `
    <div class="content">
    <h2 class="content__heading">Explore Restaurant</h2>
    <div id="makanan" class="movies">
    </div>
  </div>
      `;
  },

  async afterRender() {
    const makanans = await MakananSource.makananList();
    const makananContainer = document.querySelector('#makanan');
    makanans.forEach((makanan) => {
      makananContainer.innerHTML += createRestaurantItemTemplate(makanan);
    });
  },
};

export default Home;
